$(window).bind('load', function() {
    $('.quick-nav').removeClass('quick-nav--hidden');

	if ($(window).width() <= 1279) {
		$('body').addClass('nav-menu');
	}

	else {
		$('body').removeClass('nav-menu');
    }
    
    $(window).resize(function () {
		if ($(window).width() <= 1279) {
			$('body').addClass('nav-menu');
		}
		
		else {
			$('body').removeClass('nav-menu');
		}
    });

    $('body').bind('click focus', function(e) {
        var self = e.target;
        
        if (self.parentElement.className.search('quick-nav') < 0) {
            $('.quick-nav').removeClass('expanded');
        }
    });
    
    $('.quick-nav').bind('click focus', function(e) {
        $(this).toggleClass('expanded');
    });
});